@import url('https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap');

* {
  color: var(--stroke);
}

.game {
  background-color: var(--blue);
  width: 320px;
  height: 620px;
  border-radius: 20px 20px 50px;
  border: 6px solid var(--stroke);
  box-shadow: inset 10px 0 rgba(255,255,255,0.7), inset -10px 0 rgba(0,0,0,0.2) ;
  position: relative;
}

.header {
  width: 100%;
  height: 36px;
  border-bottom: 6px solid var(--stroke); 
  position: relative;
  box-shadow: 0 6px rgba(0,0,0,0.2) ;
}

.header:after, .header:before {
  content: "";
  height: 100%;
  width: 6px;
  background-color: var(--stroke);
  position: absolute;
}

.header:before {
  left: 30px;
}

.header:after {
  right: 30px;
}

.glass {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.screen {
  font-family: 'Press Start 2P', cursive;
  width: 246px;
  background-color: var(--light-brow);
  margin: 30px 0 0 30px;
  border: 6px solid var(--stroke);
  padding: 30px;
  border-radius: 10px 10px 30px;
  position: relative;
}

.ring {
  font-size: .7em;
}

.ring-message {
  font-size: .8em;
}

.scoreBoard {
  font-size: .5em;
}

.screen .glass {
  width: 100%;
  height: 160px;
  background-color: var(--green);
  border: 6px solid var(--stroke);
  box-shadow: inset 6px 6px rgba(0,0,0,0.2);
  overflow: hidden;
  position: relative;
  padding: 10px;
}

.glass:after {
  right: -20px;
  width: 50px !important;
}

.screen:after, .screen:before {
  content:"";
  height: 3px;
  position: absolute;
  top: 12px;
  background-color: var(--stroke); 
}

.screen:before {
  width: 100px;
  left: 10px;
}

.screen:after {
  width: 30px;
  right: 10px;
}

.buttons {
 border: 6px solid var(--stroke);
 padding: 8px 3px;
 margin: 5px;
 background-color: var(--gray);
 border-radius: 50px;
 /* transform: rotate(-20deg); */
}

 .buttonJoke {
 border: 6px solid var(--stroke);
 width: 270px;
 padding: 20px 30px;
 margin: 5px;
 background-color: var(--gray);
 border-radius: 40px;
 /* transform: rotate(-20deg); */
 text-transform: uppercase;
 font-size: 1.0em;
 box-shadow: inset 4px 0 rgba(255,255,255,0.7), inset -4px 0 rgba(0,0,0,0.2) ;
}

.buttonJoke:hover {
  background-color: var(--red);
  border: 6px solid var(--stroke);
  box-shadow: inset 4px 0 rgba(255,255,255,0.7), inset -4px 0 rgba(0,0,0,0.2) ;
}

.buttons button {
 background-color: var(--red);
 border: 6px solid var(--stroke);
 border-radius: 50%;
 width: 70px;
 height: 70px;
 padding: 0;
 box-shadow: inset 4px 0 rgba(255,255,255,0.7), inset -4px 0 rgba(0,0,0,0.2) ;
 margin: 0 5px;
}


.actions {
 padding: 30px 14px 0;
}

.instructions {
  margin: 20px;
}


.fa, .fas {
  font-size: 1.8em;
  color: var(--stroke);
}